import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Chip } from '@mui/material';
import ProgressBar from 'react-bootstrap/ProgressBar';
import Footer from '../Footer';
import db from 'libs/db';
import CurrencyInput from 'react-currency-input-field';

export default function RetirementAge(props) {

	const [retirementAge, setRetirementAge] = React.useState();
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const STATE = props.location.state

    const chipset = [
        { key: 50, name: "50 years" },
        { key: 55, name: "55 years" },
        { key: 60, name: "60 years" },
    ]

	React.useEffect(async ()=>{
		await db.get(db.model.createGoal).then(p=>{
			if(p && p.retirementAge!==undefined){
				setRetirementAge(p.retirementAge)
			} else {
				setRetirementAge("")
			}
		})
	},[])

	const checkValidation = () => {
		if (retirementAge > STATE.presentAge) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [retirementAge])

	if (STATE === undefined) {
		props.history.push('/Goals')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header={STATE.header}
				navigation={props.history}
			/>
			<ProgressBar now={30} className="progress-bar-custom" />
			<div className='dataContainer'>
				<div className="bodyTextDark" style={{ marginTop: "6%" }}>At what age do you wish to retire?</div>
				<CurrencyInput
					className='normalInput'
					id="retirementAge"
					name="retirementAge"
					placeholder="60 Years"
					value={retirementAge}
					allowDecimals={false}
					allowNegativeValue={false}
					maxLength={2}
					disableAbbreviations={true}
					suffix=' Years'
					onValueChange={(value) => setRetirementAge(Number(value) || '')}
					autoFocus
				/>
				<div className='hiddden-scrollbar'>
					{chipset.map((item) => {
						return (
							<Chip
								className={retirementAge === item.key ? "normalChip selected" : "normalChip"}
								key={item.key}
								label={item.name}
								onClick={() => {
									setRetirementAge(item.key)
								}}
							/>
						)
					})}
				</div>
			</div>
			<div className='footer'>
				<button
					className='solidButton'
					style={{ opacity: nextDisabled ? 0.4 : 1 }}
					onClick={() => {
						if(!nextDisabled){
							props.history.push({ pathname: '/Goals/monthly-expense', state: { ...STATE, retirementAge, goalDue: retirementAge-STATE.presentAge }})
						}
					}}
				>Next</button>
			</div>
			<Footer
				navigation={props.history}
				next='/Goals/monthly-expense'
				state={{ ...STATE, retirementAge ,goalDue: retirementAge-STATE.presentAge}}
				saveData = {{ retirementAge }} 
				buttonText="Next"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
